import React, { useState, useEffect } from 'react';
import { Slider } from 'components/Slider/Slider';
import {
  HomeSection,
  HomeTextMain,
  HomeTextSecondary,
  BlueCarImg,
  HomeWrapper,
  ContentWrapper,
} from './Home.styled';

import blueCar from 'images/blueCar.png';

const Home = ({ data }) => {
  const [isAnimated, setIsAnimated] = useState(false);

  useEffect(() => {
    setIsAnimated(true);
  }, []);

  return (
    <HomeSection>
      <HomeWrapper>
        <BlueCarImg src={blueCar} alt="blue car" animated={isAnimated} />
        <ContentWrapper>
          <Slider cars={data} />
          <HomeTextMain>
            Unlock the Road to Adventure with Our Car Rental Service!
          </HomeTextMain>
          <HomeTextSecondary>
            The ultimate solution for hassle-free car rentals! With a wide range
            of vehicles to choose from, our user-friendly interface simplifies
            the process of renting a car.
          </HomeTextSecondary>
        </ContentWrapper>
      </HomeWrapper>
    </HomeSection>
  );
};

export default Home;
